<template>
    <div id="backClass">
        <p>Tow Way data binding</p>
    <p>tiene sentido en componentes de formularis</p>
    <!-- cambiar desde el moelo de datos cambiar datos al modelo -->
    <input type="text" v-model="nombre">
    <p>{{ nombre }}</p>
    <button v-on:click="CambiarModelo">Cambiar Valor</button>
    </div>
   
</template>
<script>

export default {
    name : 'TowWay',
    data: function () {
        return{
            nombre : ''

        }
    },
    methods: {
       CambiarModelo : function () {
            this.nombre =  'Esto es una prueba '
       }
    }


}
</script>
<style scoped>
    #backClass{
        background-color: brown;
    }
</style>